import ReactDOM from 'react-dom';
import React from "react";
import {GreeterMessage} from "./components/GreeterMessage";
import {GreeterForm} from "./components/GreeterForm";


var Greeter = React.createClass({
    getDefaultProps: () => (
        {name: 'Anonymous', message: 'This is a default message'}
    ),

    getInitialState: function () {
        return {name: this.props.name, message: this.props.message}
    },

    render: function () {
        return (
            <div>
                <GreeterMessage name={this.state.name} message={this.state.message}/>
                <GreeterForm onNewData={this.handleNewData}/>
            </div>
        );
    },

    handleNewData: function (data) {
        event.preventDefault();
        this.setState(data)
    }
});

ReactDOM.render(
    <Greeter name="shubham"/>,
    document.getElementById('app')
)


